* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Inter, Roboto, Helvetica Neue, Arial Nova, Nimbus Sans, Arial, sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.3;
}

body {
  background-image: linear-gradient(to top, #1e4b73, #fff0);
  margin: 20px;
}

hr {
  background-image: linear-gradient(to right, #0000, #000000bf, #0000);
  border: 0;
  height: 1px;
}

h1 {
  font-family: Super Lobster, Comic Sans ms, cursive;
  font-size: 4em;
}

h2 {
  font-size: 1.5em;
}

#raceDetails {
  flex-direction: row;
  justify-content: space-evenly;
  column-gap: 5em;
  display: flex;
}

#raceDetails img {
  width: auto;
  height: 33%;
  max-height: 250px;
}

#podiumAndTable {
  flex-direction: row;
  justify-content: space-evenly;
  column-gap: .5em;
  display: flex;
}

#podium {
  width: 50%;
  height: auto;
  max-height: 500px;
}
